.edit__book {
  border-radius: 25px;
  padding: 0 0;
  display: flex;
  flex-direction: column;
  margin: 0 2.5%;
}
.edit__book__upload {
  display: flex;
  gap: 30px;
  align-items: end;
  margin-bottom: 30px;
}
.edit__book__upload > div {
  width: 200px;
}
.edit__book__upload > div > input {
  display: none;
}
.edit__book__upload > div > label {
  font-size: var(--font-size-secondary);
  font-weight: 600;
  margin-bottom: 1px;
}
