.ranking {
  background: var(--color-border);
}
.ranking__section {
  min-height: 100vh;
  background: var(--color-border);
  padding: 1.5% 15%;
  width: 100%;
  border-radius: 0px 0px 120px 120px;
}
.ranking__section__one {
  display: flex;
  align-items: start;
  justify-content: space-between;
  gap: 30px;
  margin-top: 20px;
}
.ranking__section__one__nav {
  width: 20%;
  position: -webkit-sticky;
  position: sticky;
  background: white;
  border-radius: 20px;
  padding: 25px 20px;
}
.ranking__section__one__nav > h1 {
  color: var(--color-secondary-accent);
  margin-bottom: 30px;
  font-size: 20px;
  font-weight: 800;
  line-height: 24.2px;
  text-align: left;
}
.ranking__section__one__nav__active {
  background: var(--color-primary-accent);
  color: white;
  padding: 10px;
  border-radius: 8px;
}
.ranking__section__one__nav > h3 {
  margin-bottom: 15px;
  font-size: var(--font-size-secondary);
  font-weight: 700;
  line-height: 16.94px;
}
.ranking__section__one__body {
  width: 80%;
  background: white;
  border-radius: 20px;
  padding: 20px 20px;
  min-height: 100svh;
}
.ranking__section__one__body__display {
  padding: 30px 0;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 30px;
}
.ranking__section__one__body__display__block {
  display: flex;
  align-items: center;
  gap: 15px;
  width: 90%;
}
.ranking__section__one__body__display__block > h1 {
  font-size: 25px;
  color: #777;
  margin: 0 25px;
  font-weight: 500;
}
.ranking__section__one__body__display__block > article {
  display: flex;
  align-items: center;
  gap: 20px;
  margin: 0 20px;
}
.ranking__section__one__body__display__block > article > button {
  border-radius: 30px;
  border: 1px solid var(--color-secondary-accent);
  background: white;
  color: var(--color-secondary-accent);
  padding: 5px 20px;
}
.ranking__section__one__body__display__block > article > button:last-of-type {
  background: var(--color-secondary-accent);
  color: white;
}
.ranking__section__one__body__display__block > img {
  width: 128px;
  height: 181.07px;
}
.ranking__section__one__display__block__about {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.ranking__section__one__display__block__about > h1 {
  font-size: var(--font-size-secondary);
  font-weight: 700;
  line-height: var(--line-height-primary);
  text-align: left;
}
.ranking__section__one__display__block__about > span {
  display: flex;
}
.ranking__section__one__display__block__about > span > h1 {
  font-size: var(--font-size-secondary);
}
.ranking__section__one__display__block__about > span > h2 {
  font-size: var(--font-size-secondary);
  margin-left: auto;
}
.ranking__section__one__display__block__about > p {
  font-size: var(--font-size-secondary);
  font-weight: 300;
  line-height: var(--line-height-primary);
}
.ranking__section__one__display__block__about > span:last-of-type > h1 {
  font-size: var(--font-size-secondary);
  font-weight: 600;
  line-height: 14.52px;
  color: var(--color-secondary-accent);
}

@media all and (max-width: 1024px) {
  .ranking__section {
    padding: 5% 5%;
  }
  .ranking__section__one {
    flex-direction: column;
    align-items: start;
    justify-content: start;
    gap: 10px;
  }
  .ranking__section__one__nav {
    width: 100%;
  }
  .ranking__section__one__body {
    width: 100%;
    padding: 5% 5%;
  }
  .ranking__section__one__body__display__block {
    flex-direction: column;
    align-items: start;
    gap: 15px;
    width: 100%;
    border-bottom: 1px solid #eee;
    padding-bottom: 20px;
  }
  .ranking__section__one__body__display__block > h1 {
    font-size: 22px;
    margin: 0;
  }
  .ranking__section__one__body__display__block > article {
    margin: 0;
  }
}
